import React from 'react';

export default class ModalForm extends React.Component {

    state = {
        values : {}
    }

     setValues(index, val){
        let arr = {...this.state.values};
        arr[index] = val;
        this.setState({values : arr});
    }

    closeModal(){
        this.props.closeModalForms();
    }

    saveValue(){
        console.log('save value');
        console.log(this.state.values);
    }

    componentDidMount() {
        console.log('component did mount');
        if (this.props.refCode === 0) {
            this.setState({values : this.props.refData});
        }
    }

    render (){
        console.log('render');
        let stylemodal = {};
        let stylecontainer = {};
        let str = [];
        let strbt = [];
        let j = 0;
        if (this.props.refCode === 0) {
            stylemodal = { width : '300px', 
                height : '100px', 
                top: (window.innerHeight / 2 - 50),
                left : (window.innerWidth / 2 -150)
            };
            stylecontainer = { width : '300px', height : '100px', overflow : 'visible'};
            for (var i in this.state.values){
                 str[j] = <p>
                        <label> {this.props.refHeader[i]} </label>
                        <input type='password' data-key = {i} value = {this.state.values[i]} onChange= {(e) => {this.setValues(e.target.getAttribute('data-key'),e.target.value)}} />
                    </p>;
                j++;
            }
            str[j] = <p>
                        <button onClick = {() => {this.saveValue()}}>Зберегти</button>
                        <button onClick = {() => {this.closeModal()}}>Відмінити</button>
                    </p>;
           
        } else
        if (this.props.refCode === 2) {
            stylemodal = { width : '500px', 
            height : '500px', 
            top: (window.innerHeight / 2 - 250),
            left : (window.innerWidth / 2 -250)
        };
        stylecontainer = { width : '500px', height : '400px', overflow : 'scroll'};
       let hdr = this.props.refHeader.map((a,index) => {return <th key = {index}>{a.name_col}</th>});
            let thr = null;
            let dtr = null;
            console.log('----//----//-----///------')
            console.log(this.props.refData);
            console.log(this.props.refData[0]['checkes']);
            if (typeof this.props.refData[0]['checkes'] !== 'undefined'){
                console.log('1');
                thr = <tr> <th key = {-1}></th> {hdr} </tr>;
            } else {
                console.log('2');
                thr = <tr> {hdr} </tr>;
            }
            dtr = this.props.refData.map((a, index) => {
                let i = 0;
                let dd = [];
                for ( let k in a){
                    dd[i] = <td key = {i}>  {a[k]}  </td>;
                    i++;
                }
                return <tr key = {index}> {dd} </tr>;
            });
            str = <table className='TableDespatch'> 
                    {thr}
                    {dtr}
                  </table>;
            strbt = <p>
                        <button onClick = {() => {this.saveValue()}}>Зберегти</button>
                        <button onClick = {() => {this.closeModal()}}>Відмінити</button>
                    </p>;

        }
        
        return  <> <div id="modal_form" style = {stylemodal}>
                    <div style = {{height : '10px'}}><span id="modal_close" onClick = {()=>{this.closeModal()}}>X</span></div>
                    <div className = "container" style = {stylecontainer}>{str}</div>
                    <div>{strbt}</div>
               </div>
                <div id="overlay"></div></>
                
    }
} 