import React from 'react';
import TableRow from './tablebody';
import ModalForm from './../Modal/modalform';


let Tables = ({tableHeader, tableData, dovbutton, selectdov, selectdovrow, dovButtons, selectRow, setEditRow, clickButton, deleteRow, isModal, refId, refCode, refType, refFileResult, refHeader, refData, closeModalForms}) => {

    let TableHeaderButton = dovbutton.map((a, index) => { if (a.in_row) return <th key = {'b'+index}></th>});


    let TableHeader = tableHeader.map((a, index) => {
        return <th key = {index}>{a.name_col}</th>
    });

    let TableBody = tableData.map((a, index) => { 
        return <TableRow tableHeader = {tableHeader} 
                         dovbutton = {dovbutton}
                         tablerow = {a} 
                         index = {index} 
                         selectdovrow = {selectdovrow} 
                         selectRow = {selectRow} 
                         selectDov = {selectdov}
                         setEditRow={setEditRow} 
                         clickButton={clickButton}
                         deleteValue={deleteRow}
                         isModal = {isModal}
                         refId = {refId}
                         refCode = {refCode}
                         refType = {refType}
                         refFileResult = {refFileResult}
                         refHeader = {refHeader}
                         refData = {refData}
                         />;        
    });

    let TableAddRow = tableHeader.map((a, index) => {
        return <td key = {index}><input type = 'text' className = 'dovinputtable'/></td>
    });
    let TableButon = dovbutton.map((a,index) => { if (a['in_row'] === 1) return <td key = {'b'+index}></td>});

    let T = () => {
        if ( tableHeader.length > 0 ){
            return <table className='TableDespatch'>  
                    <tbody>
                        <tr key = '-2'> 
                            {TableHeader}
                            {TableHeaderButton} 
                        </tr>
                        <tr key = '-1'>
                            {TableAddRow} 
                            {TableButon}
                        </tr>
                        {TableBody}
                    </tbody>
                </table>
        } else {
            return '';
        }
    }
    let DB = dovButtons.map((a, index) => {
        if (!a.in_row) return <button key = {index} value = {a.id_dovidnuk}>{a.name_button}</button>
        return null;
    });

    let ModalForms = () => { 
        if (isModal === 1) {
            return <ModalForm refId = {refId}
                            refCode = {refCode}
                            refType = {refType}
                            refFileResult = {refFileResult}
                            refHeader = {refHeader}
                            refData = {refData}
                            closeModalForms = {closeModalForms}
            />
        }
    }

    return(
        <div>
           {DB}
           {T()}
           {ModalForms()}
        </div>
    )
}

export default Tables;